@keyframes fadeIn {
  0% {
    opacity: 0; }
  100% {
    opacity: 1; } }

@keyframes fadeInDown {
  0% {
    opacity: 0;
    transform: translateY(20px); }
  100% {
    opacity: 1;
    transform: translateY(0px); } }

@keyframes hexagonAnimation {
  0% {
    transform: rotate(0deg) scale(0.5);
    opacity: 0; }
  100% {
    transform: rotate(45deg) scale(1);
    opacity: 1; } }

@keyframes centerVerticalLineAnimation {
  0% {
    transform: rotate(-45deg) scale(0.5);
    opacity: 0; }
  100% {
    transform: rotate(0deg) scale(1);
    opacity: 1; } }

@keyframes leftVerticalLineAnimation {
  0% {
    left: calc(50% - 3px);
    opacity: 0; }
  1% {
    opacity: 1; }
  100% {
    left: calc(50% - 23px);
    opacity: 1; } }

@keyframes letterLineAnimation {
  0% {
    opacity: 0;
    transform: rotateY(90deg); }
  1% {
    opacity: 1; }
  100% {
    opacity: 1;
    transform: rotateY(0deg); } }

@keyframes rightVerticalLineAnimation {
  0% {
    right: calc(50% - 3px);
    opacity: 0; }
  1% {
    opacity: 1; }
  100% {
    right: calc(50% - 23px);
    opacity: 1; } }

@keyframes arrowDrawIn {
  from {
    transform: translateY(-20px);
    stroke-dashoffset: 85; }
  to {
    transform: translateY(0px);
    stroke-dashoffset: 0; } }

body {
  background: #001b2b; }

.portfolio {
  height: 200vh; }
  .portfolio .hero-background-container {
    width: 100vw;
    height: 100vh;
    position: fixed;
    z-index: -100;
    top: 0;
    overflow: hidden;
    opacity: 0;
    -webkit-animation: fadeIn 2s 2.5s forwards;
    animation: fadeIn 2s 2.5s forwards; }
    .portfolio .hero-background-container .hero-background-image {
      width: 100%;
      height: 100%;
      background-image: url("./images/ash-wood-background.png");
      background-size: cover;
      background-position: 60%;
      position: absolute; }
    .portfolio .hero-background-container .hero-background-gradient {
      width: 100%;
      height: 100%;
      background-color: #0000ff;
      background-image: -webkit-linear-gradient(315deg, #00ffff 0%, #0000ff 50%, #00ffff 100%);
      background-image: -o-linear-gradient(315deg, #00ffff 0%, #0000ff 50%, #00ffff 100%);
      background-image: linear-gradient(135deg, #00ffff 0%, #0000ff 50%, #00ffff 100%);
      opacity: 0.8;
      position: absolute;
      z-index: 1; }
  .portfolio .hero-data-container {
    display: -ms-flexbox;
    display: flex;
    -ms-flex-direction: column;
    flex-direction: column;
    -ms-flex-align: center;
    align-items: center;
    padding: 0 20px;
    height: 100vh;
    position: relative; }
    .portfolio .hero-data-container .hero-intro-text {
      color: white;
      text-align: center;
      font-size: 2.25rem;
      opacity: 0;
      -webkit-animation: fadeInDown 2s 2.5s forwards;
      animation: fadeInDown 2s 2.5s forwards; }
    .portfolio .hero-data-container .hero-arrow {
      width: 25px;
      height: 25px;
      stroke: white;
      margin-top: 2px;
      fill: transparent;
      stroke-width: 10px;
      overflow: unset !important;
      stroke-dasharray: 85;
      stroke-dashoffset: 85;
      position: absolute;
      bottom: 80px;
      left: calc(50% - 12.5px);
      -webkit-transition: 0.5s;
      -o-transition: 0.5s;
      transition: 0.5s;
      opacity: 1;
      -webkit-animation: arrowDrawIn 1.5s 4.5s forwards;
      animation: arrowDrawIn 1.5s 4.5s forwards; }
      .portfolio .hero-data-container .hero-arrow.hidden {
        opacity: 0;
        bottom: 100px; }



